<template>
    <center>
        <br>
        <br>
        <br>
        <br>
        <p style="color:black">Step 1</p>
        <p style="color:black">Connect Your Solana Wallet</p>
        <br>
        <wallet-multi-button dark></wallet-multi-button>
        <br>
        <br>
        <p style="color:black">Step 2</p>
        <p style="color:black">Sign Message to Verify Wallet</p>
        <br>
        <button class="btn-normal" @click="signMsg">Sign Message</button>
    </center>
</template>

<script>
    import { WalletMultiButton, useWallet } from 'solana-wallets-vue'
    import axios from 'axios'

    export default {
        components: {
            WalletMultiButton,
        },
        setup() {
            const { publicKey, signMessage } = useWallet();

            const getProvider = () => {
                if ('phantom' in window) {
                    const provider = window.phantom?.solana;

                    if (provider?.isPhantom) {
                        return provider;
                    }
                }
            };

            var signFlag = true;

            if (!publicKey.value) {
                signFlag = false;
            }
            else {
                signFlag = true;
            }

            const checkSignFlag = () => {
                if (!publicKey.value) {
                    signFlag = false;
                    console.log("false");
                }
                else {
                    signFlag = true;
                    console.log("true");
                }
            }

            const signMsg = async () => {

                if (!publicKey.value) {
                    return alert('Connect your wallet first.')
                }
                
                if (!signMessage) {
                    return alert('Wallet Not supported')
                }

                const provider = getProvider();
                const message = new TextEncoder().encode('UniFy');
                
                const signature = await provider.signMessage(message, "utf8");
                if (!signature) {
                    return alert('No signature')
                }
                else {
                    //call model to verify and save
                    //var baseAdd = window.location.origin;
                    //var url = baseAdd + "/Home/WalletAdd" + "/" + message + "/" + signature + "/" + publicKey.value.toBytes();
                    //publicKey.value.toBase58()
                    //console.log(publicKey.value);
                    //window.location.href = url;

                    var pk = new TextEncoder().encode(publicKey.value.toBase58());

                    axios({
                        method: 'post',
                        //url: '/Wallets/AddWallet',
                        url: '/Profiles/AddWallet',
                        data: {
                            "message": btoa(String.fromCharCode.apply(null, message)),
                            "signature": btoa(String.fromCharCode.apply(null, signature.signature)),
                            "pub": btoa(String.fromCharCode.apply(null, pk)),
                            "ver": btoa(String.fromCharCode.apply(null, publicKey.value.toBytes()))
                        }
                    })
                        .then(function (response) {
                            if (response) {
                                window.location.href = '/';
                            }
                            else
                            {
                                return alert('Verification Failed')
                            }
                        })
                        .catch(function (error) {
                            console.log(error);
                        });



                }

            }

            return {
                signMsg,
                signFlag,
                checkSignFlag,
            }

        }
    }

</script>

<style>

    .btn-normal {
        background-color: #777777;
        border: none;
        color: white;
        padding: 20px;
        text-align: center;
        text-decoration: none;
        display: inline-block;
        font-size: 16px;
        margin: 4px 2px;
        border-radius: 8px;
    }

        .btn-normal:hover {
            background-color: #565656;
        }
</style>
